.content-center {
  width: 1140px;
  margin: 0 auto;
}

header, footer {
  width: 100%;
  background: #f4f7fc;
}

header {
  padding-top: 3em;
  padding-bottom: 1em;
  font-weight: 500;
}

header input {
  width: 405px;
  height: 53px;
  margin-right: 65px;
}

header #url {
  padding-left: .75em;
}

header #port {
  padding-left: 1.5em;
}

header table {
  width: 100%;
  border: none;
}

header table td {
  margin: 0;
  padding: 0;
}

input {
  height: 3em;
  box-sizing: border-box;
  border: 1px solid #8d9196;
  border-radius: 4px;
  font-family: Prompt;
  font-size: 1rem;
}

button.houselist-btn {
  color: #fff;
  cursor: pointer;
  width: 200px;
  height: 53px;
  border-style: none;
  border-radius: 4px;
}

button.houselist-btn#connect-btn {
  background: #3c64b1;
}

button.houselist-btn#create-btn {
  background: #2b6;
}

#content {
  padding-bottom: 2.5em;
}

#content #content-header {
  justify-content: space-between;
  align-items: center;
  margin-top: 1em;
  font-size: 1.5em;
  display: flex;
}

#content #content-header #label {
  font-weight: 500;
}

#content #houselist-table {
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  border: 1px solid #e0e0e0;
  margin-top: 2em;
}

#content #houselist-table tr:hover {
  background: #f4f7fc;
}

#content #houselist-table td {
  height: 4em;
  border: 1px solid #e0e0e0;
}

#content #houselist-table td:first-child {
  width: 4em;
}

#content #houselist-table td:nth-child(2) {
  width: 400px;
}

#content #houselist-table td:nth-child(4) {
  width: 200px;
}

#content #houselist-table td:last-child {
  width: 270px;
}

#content #houselist-table td:last-child div {
  cursor: pointer;
  border-radius: 20px;
  padding: .5em 1.5em;
  font-size: 14px;
  display: inline-block;
}

#content #houselist-table td:last-child #view-detail-btn {
  color: #f90;
  background: #fff7e6;
  margin-right: 5px;
}

#content #houselist-table td:last-child #delete-btn {
  color: #b93e5c;
  background: #fdf4f7;
}

#content #houselist-table th {
  height: 4em;
  border: 1px solid #e0e0e0;
  font-weight: 700;
}

#content #rpp-selector {
  text-align: right;
  margin-top: 1em;
}

#content #rpp-selector select {
  border: none;
  margin-right: 1em;
  font-family: Prompt;
}

#content #rpp-selector img {
  cursor: pointer;
}

#content #rpp-selector #previous-page {
  margin-right: 3.5em;
}

footer {
  height: 235px;
  flex-direction: column;
  align-items: center;
  display: flex;
}

footer #select-container #postcode-select {
  width: 405px;
  margin-top: 65px;
}

footer #postcode-detail {
  color: #3c64b1;
  font-weight: 600;
}

.modal-content {
  padding: 1.75em 1.5em;
}

.modal-content .my-modal-header {
  margin-bottom: .25em;
  font-weight: 500;
}

.modal-content .btn-toolbar {
  justify-content: center;
  display: flex;
}

.modal-content button {
  width: 164px;
}

.modal-content #upsert-cancel-btn {
  border: 1px solid #c0c5ce;
}

.modal-content #upsert-confirm-btn {
  background: #2b6;
}

.modal-content #result-icon {
  width: 75px;
  height: 75px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.modal-content #result-icon.success {
  background: #2b6;
}

.modal-content #result-icon.fail {
  background: #b93e5c;
}

.result-modal .modal-dialog {
  width: 331px !important;
}

.result-modal .modal-dialog .modal-content {
  color: #8d9196;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3em 0;
  display: flex;
}

.result-modal .modal-dialog .modal-content #result-label {
  font-size: 1.5em;
  font-weight: 700;
}

.result-modal .modal-dialog .modal-content #result-description {
  font-weight: 600;
}

.result-modal .modal-dialog button {
  border: 1px solid #c0c5ce;
  margin-top: 2em;
  padding: .75em;
}

